@import url(https://fonts.googleapis.com/css?family=Open+Sans);
@import url(https://fonts.googleapis.com/css?family=Google+Sans);
@import url(https://fonts.googleapis.com/css?family=Roboto+Mono);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    -webkit-animation: App-logo-spin infinite 20s linear;
            animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}


body{
    font-family: 'Google Sans',serif;
}
h2,h3,h4{
    -webkit-margin-before: 0.43em;
            margin-block-start: 0.43em;
    -webkit-margin-after: 0em;
            margin-block-end: 0em;
}
.container{
    padding-top: auto;
    padding-left:10%;
    padding-right:10%;
    padding-bottom:2%;
    overflow: hidden;

}
img{
    transition: -webkit-transform 1.5s ease-out;
    transition: transform 1.5s ease-out;
    transition: transform 1.5s ease-out, -webkit-transform 1.5s ease-out;
}
img:hover{
    display: block;
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
    transition: -webkit-transform 1.5s ease-out;
    transition: transform 1.5s ease-out;
    transition: transform 1.5s ease-out, -webkit-transform 1.5s ease-out;
    object-fit: cover;
}
.grad-orange{
    background-image: linear-gradient(to right,#FF6E00,#FF6E00,#FE9001);
}
.grad-progress{
    background-image: linear-gradient(to right,#00ff9d,#00ff9d,#01e0fe);
}
.orange{
    color:#FF6E00;
}

.dark{
    background-color: #191B1B;
    color: #B2ADA2;
}
.white{
    color:white;
}
.section-title{
    font-family: 'Roboto Mono', sans-serif;
    font-size: 2.2em;
    text-align: center;
    
}

.card{
    
    text-align: center;
    border: 0px solid white;
    border-radius: 15px;
    background-color: white;
    width: 70%;
    height: 20em;
    margin-left: auto;
    margin-right: auto;
    overflow: hidden;
    padding-bottom: 8%;
}
.card-space{
    transition: -webkit-transform 2s ease;
    transition: transform 2s ease;
    transition: transform 2s ease, -webkit-transform 2s ease;
}
.card-space:hover{
    -webkit-transform: scale(1.02);
            transform: scale(1.02);
  transition: -webkit-transform 2s ease;
  transition: transform 2s ease;
  transition: transform 2s ease, -webkit-transform 2s ease;
}
.card-image{
    width: 100%;
    
    object-position: 5% 95%;
}
.card-body{
    padding-left: 1%;
    padding-right: 1%;
    padding-top: 4%;
    padding-bottom: auto;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 2%;
    width: 80%;
}
.card-body p{
    text-align: justify;
    font-size: medium;
}
.card-top{
    overflow: hidden;
    text-align: center;
    height:40%;
}
.git{
    position: absolute;
    top: -15%;
    right: -10%;
    background-color: white;
    border:1px solid white;
    border-radius: 50%;
    padding: auto;
}

.git-link{
    color: black;
}
.git-link:hover{
    color: blue;
    
}

.entry{
    
    border: 0px solid white;
    border-radius: 10px;
    transition: background-color 2s ease;
    margin-top: 0%;
    margin-bottom: 0%;
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    

}
.light-entry{
    background-color: white;
    color: black;
  transition: background-color 1s ease;
  border:0px solid #B2ADA2;
  border-radius: 12px;
}
.light-entry:hover{
    background-color: rgb(241, 241, 241);
    color: black;
  transition: background-color 1s ease;
  border:0px solid #B2ADA2;
  border-radius: 12px;
}
.dark-entry:hover{
    background-color: rgb(43, 41, 41);
    color: rgb(197, 194, 194);
  transition: background-color 2s ease;

}



.inline{
    display: flex;
}

.space-between{
    justify-content: space-between;
}
.space-around{
    justify-content: space-around;
}
.logo{
    justify-content: center;
    padding: 1%;
    height: 8em;
    width: 8em;
    margin-left: 1%;
    margin-right: 1%;
    margin-top: 0%;
    background-color: white;
    border: 1px solid white;
    border-radius: 20px;
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    overflow: hidden;
}
.logo img{
    -webkit-transform: scale(0.7);
            transform: scale(0.7);
    transition: -webkit-transform 1.5s ease;
    transition: transform 1.5s ease;
    transition: transform 1.5s ease, -webkit-transform 1.5s ease;
}
.logo img:hover{
    -webkit-transform: scale(0.8);
            transform: scale(0.8);
    transition: -webkit-transform 1.5s ease;
    transition: transform 1.5s ease;
    transition: transform 1.5s ease, -webkit-transform 1.5s ease;
}

.details{
    padding-top: 1%;
    padding-bottom: 1%;
}
.skills{
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    grid-gap: 1rem;
    gap: 1rem;

}
.skill-card{
    width: 80%;
    margin: 1%;
    padding: 1%;
}

.pro-pic{
    width:10em;
    height: 10em;
    align-items: center;
    padding-top:auto;
    padding-bottom: auto;
    border: 0px solid white;
    background-color:black;
    border-radius: 50%;
    overflow: hidden;
    vertical-align: middle;
    margin: 1%;
    margin-left: 5%;
    margin-right: 5%;
    
    margin-bottom: -5%;
}
.pro-pic img{
    display: block;
    -webkit-transform: scale(2.5);
            transform: scale(2.5);
    width:90%;
    
}

.about{
    width: 70%;
    border: 0px solid white;
    background-color: white;
    border-radius: 15px;
    padding: 2%;
    height: -webkit-fit-content;
    height: -moz-fit-content;
    height: fit-content;
    text-align: justify;
    margin: 1%;
}
.about .section-title{
    margin-top: 0;
    margin-bottom: 2%;
}

.example{
    position: absolute;
    width: 20em;

}
.stuff{
    position:absolute;
    top: 1%;
    padding-left: 5%;
    padding-right: 5%;
}

.head-back{
    height: 20em;
    overflow: hidden;
}
.stuff-head{
    text-align: center;
}
.stuff-head p{
    font-family: 'Roboto Mono',sans-serif;
    font-size: xx-large;
}

@media screen and (max-width:700px){
    
    .head-back{
        height: 35em;
    }
    .inline{
        display:block;
        justify-content: center;
    }
    .logo{
        display: block;
    margin-left: auto;
    margin-right: auto;
    }
    .container{
        padding-left:5%;
        padding-right: 5%;
    }
    .entry{
        text-align: center;
        padding-top:5% ;
        border: 1px solid #B2ADA2;
        border-radius: 10px;
        margin: 5%;
        padding: 5%;
        -webkit-transform: scale(0.8);
                transform: scale(0.8);
        
    }
    .card{
        width: 80%;
    }
    .card-body p,h4{
       font-size: small;
    }
    .card-body h3{
        font-size: medium;
     }
     .pro-pic,.about{
         margin-left: auto;
         margin-right: auto;
         
     }
     .about{
        padding: 5%;
     }

}


.inline-first{
    display:flex;
}
.inline-second{
    display:flex;
}

@media screen and (max-width:1400px){
    .inline-first{
        display: block;
    }
}
@media screen and (max-width:700px){
    .inline-second{
        display: block;
    }
}

.progress-div {
    background-color: rgb(207, 207, 207);
    border-radius: .5rem;
    height: 18px;
    width: 50%;
}

.progress {
  color: gray;
  font-size: small;
  text-align: center;
  padding-top: auto;
  padding-bottom: auto;
  background-color: rgb(62, 122, 235);
  height: 18px;
  border-radius: 1rem;
  transition: 1s ease;
  transition-delay: 0.5s;
}
